import {Injectable, Optional} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable} from 'rxjs';
import {ServicesConfig} from '../services.config.interface';
import {MercurioBody, MercurioRequest, MercurioResponse} from 'cibeles/models';


@Injectable()
export class MercurioService<T> {
  private httpOptions: { headers: HttpHeaders };
  private url: string;

  constructor(@Optional() config: ServicesConfig, private httpClient: HttpClient) {

    this.httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/x-www-form-urlencoded'
      })
    };

    this.url = config.servers.mercurio;
  }

  post(request: T): Observable<MercurioResponse> {

    const body: MercurioBody<T> = {
      Entrada: request,
      Id: '',
      Metodo: '',
      Servicio: 'servicios',
      Tipo: '',
      URL: this.url
    };

    return this.httpClient.post<MercurioResponse>(this.url, body, this.httpOptions);
  }

  get(request: MercurioRequest): Observable<MercurioResponse> {
    return this.httpClient.get<MercurioResponse>(this.url);
  }
}
