import {Injectable} from '@angular/core';
import {MercurioService} from './mercurio.service';
import {Observable, Subject} from 'rxjs';
import {MercurioResponse, LoginResponse, LoginRequest} from 'cibeles/models';
import {Service} from '../service.interface';

@Injectable()
export class LoginService implements Service {

  private loginSubject: Subject<any>;

  constructor(private mercurioService: MercurioService<LoginRequest>) {
    this.loginSubject = new Subject<any>();
  }

  request(request: LoginRequest) {
    this.mercurioService.post(request).subscribe(
      (response: MercurioResponse) => this.loginSubject.next(response.Salida as LoginResponse)
    );
  }

  get(): Observable<LoginResponse> {
    return this.loginSubject.asObservable();
  }
}
